import React, { useState, useEffect } from 'react'
import personService from './services/personService'
import Notification from './components/Notification'


const App = () => {
  const [ persons, setPersons ] = useState([]) 
  const [ newName, setNewName ] = useState('')
  const [ newNum, setNewNum ] = useState('')
  const [ search, setSearch ] = useState('')
  const [ notification, setNotification] = useState(null)
  const [errorMessage, setErrorMessage] = useState(null)

  useEffect(() => {
    personService.getAll()
    .then(response => setPersons(response.data))
  },[])

  const addVal = (event) => {
    event.preventDefault()
    const avl_names = persons.map(person => person.name)
    if (avl_names.includes(newName)) {
      const avl_details = persons.find(person => person.name === newName)
      const res = window.confirm(`${newName} is already added to the phonebook, replace the old number with a new one?`)
      if (res===true) {
        const changedData = {...avl_details, number: newNum}
        personService.update(avl_details.id, changedData)
        .then((response) => {setPersons(persons.map(person => person.id !== avl_details.id ? person : response.data))
        setNewNum('')
        setNewName('')
        setNotification(`Added ${avl_details.name}`)
        setTimeout(() => {
          setNotification(null)
        }, 5000)
      })
        .catch((err) => {
          if (err.response.data) {
            setErrorMessage(err.response.data.error)
            setTimeout(() => {
              setErrorMessage(null)
            }, 5000)
          } else {
            setErrorMessage(
              `Information of ${avl_details.name} has already been removed from server`
            )
            setTimeout(() => {
              setErrorMessage(null)
            }, 5000)
          }
        })
      }
      return
    }
    const valObject = {
      name: newName,
      number: newNum
    }
    
    personService.create(valObject)
    .then(response => {
      setPersons(persons.concat(response.data))
      setNewNum('')
      setNewName('')
      setNotification(`Added ${response.data.name}`)
        setTimeout(() => {
          setNotification(null)
        }, 5000)
    })  
  }

  const deleteRecord = (record) => {
    if (window.confirm(`Delete ${record.name}`)) {
      personService.deleteA(record.id).then(() => setPersons(persons.filter(person => person.id !== record.id)))
    }
    
  }


  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }

  const handleNumChange = (event) => {
    setNewNum(event.target.value)
  }

  const handleSearch = (event) => {
    setSearch(event.target.value)
  }

  const toShow = search === '' ? persons : persons.filter(person => person.name.toLowerCase().includes(search.toLowerCase()))

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification notification={notification} errorMessage={errorMessage} />
      <Filter value={search} onChange={handleSearch} />
      <h3>Add a new</h3>
      <PersonForm onSubmit={addVal} name={{value: newName, onChange: handleNameChange}} number={{value: newNum, onChange: handleNumChange}} />
      <h2>Numbers</h2>
      <ul>
        {toShow.map(person =>
        <Details key={person.id} name={person.name} number={person.number} delte = {() => deleteRecord(person)}/>
        )}
      </ul>
    </div>
  )
}

const PersonForm = ({onSubmit, name, number}) => {
  return (
    <form onSubmit = {onSubmit}>
        <div>
          name: <input value={name.value} onChange = {name.onChange}/>
        </div>
        <div>
          number: <input value={number.value} onChange={number.onChange}/>
        </div>
        <div>
          <button type="submit">add</button>
        </div>
    </form>
  )
}

const Filter = ({value, onChange}) => {
  return (
    <div>
        filter shown with: <input value={value} onChange = {onChange}/>
    </div>
  )
}

const Details = ({name, number, delte}) => {
  return (
    <li>{name} {number} <button onClick={delte}>delete</button></li>
  )
}

export default App